import React,{Component} from 'react';
import './App.css';
import Markdown from 'react-markdown';
import CodeBlock from "./CodeBlock";
import Img from './images'
import artList from './data.js'
export default class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      artList: artList.reverse(),
      markdown: '',
      isShowDetail: false,
      scrollTop: 0
    }
  }
  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll)
  }
  handleScroll = (e) => {
    //滚动条高度
    if(!this.state.isShowDetail){
      let scrollTop  = document.documentElement.scrollTop;  //滚动条滚动高度
      this.setState({
        scrollTop
      })
    }
  }
  showDetail = (md) => {
    let scrollTop = document.documentElement.scrollTop
    sessionStorage.setItem('scrollTop', scrollTop)
    document.documentElement.scrollTop = 0
    fetch(md)
    .then(res => res.text())
    .then(text => this.setState({ markdown: text, isShowDetail: true }));
    
  }
  handleBack = () => {
    this.setState({
      isShowDetail: false
    },() => document.documentElement.scrollTop = sessionStorage.getItem('scrollTop'))
  }
  render() {
    return (
      <div className="App">
          {
            !this.state.isShowDetail?this.state.artList.map(item => {
              return <div key={item.id} className='art_list_item'>
                        <div className="art_list_item_content" onClick={this.showDetail.bind(this,item.md)}>
                          <div className='art_list_item_content_title'>{item.title}</div>
                          <div className='art_list_item_content_desc'>{item.desc}</div>
                        </div>
                        <div className='art_list_item_content_time'>
                          <div className="art_list_item_tag">
                            {
                              item.tag.map(tagItem => {
                                return <div className='art_list_item_tag_item'>{tagItem}</div>
                              })
                            }
                          </div>
                          <div className="art_list_item_date"><img src={Img.time}/><div>{item.date}</div></div>
                        </div>
                      </div>
            })
            :''
          }
          {
            this.state.isShowDetail?
            <div style={{width: '100%'}}>
              <div className="back_to_list" onClick={this.handleBack}><img src={Img.back}/><div>back</div></div>
              <Markdown 
              source={this.state.markdown} 
              escapeHtml={false}
              renderers={{
                code: CodeBlock
              }}
              />
            </div>
            :''
          }
          
          
      </div>
    )
  }
}
